.actorsList {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}
.actorItem {
  display: flex;
  flex-direction: column;

  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.imgActor {
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.imgActor:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}
.title {
  padding: 10px;
  font-family: 'Times New Roman', Times, serif;
  font-size: 20px;
  margin: 0 auto;
}
.character {
  font-family: 'Times New Roman', Times, serif;
  font-size: 18px;
  margin: 0 auto;
  text-align: center;
}
